@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.homepage{align-items:center;display:flex;flex-direction:column;padding:20px 80px}

.menu-item{align-items:center;border:1px solid #000;box-shadow:3px 3px 15px #4a4a4a;display:flex;flex:1 1 auto;height:240px;justify-content:center;margin:0 7.5px 15px;min-width:30%;overflow:hidden;position:relative}.menu-item.large{height:380px}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:.9}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{background-position:center;background-size:cover;height:100%;position:absolute;width:100%}.menu-item .content{align-items:center;background-color:#fff;border:1px solid #000;display:flex;flex-direction:column;height:90px;justify-content:center;opacity:.7;padding:0 25px}.menu-item .content .title{color:#4a4a4a;font-family:'Dancing Script', cursive;font-size:22px;font-weight:bold;margin-bottom:6px;text-transform:uppercase}.menu-item .content .subtitle{font-family:'Abril Fatface', cursive;font-size:16px;font-weight:lighter;opacity:.8}

.directory-menu{display:flex;flex-wrap:wrap;justify-content:space-between;width:100%}

